import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFacebookSquare, faInstagram } from '@fortawesome/free-brands-svg-icons';
import Hamburger from './Hamburger';
import Option from './Option';

const Menu = () => {

    const [barWidth, setBarWidth] = useState('0');
    const [menuState, setMenuState] = useState(false);
    const [socialColor1, setSocialColor1] = useState('white');
    const [socialColor2, setSocialColor2] = useState('white');
    const [displayMenu, setDisplayMenu] = useState('none');


    const startMenu = () => {
        if (barWidth === '0') {
            setBarWidth(400);
            setMenuState(true);
            setDisplayMenu('flex');
        } else if (barWidth > '0') {
            setBarWidth('0');
            setMenuState(false);
            setDisplayMenu('none');
        }
    };

    const styles = {
        container: {
            position: 'fixed',
            right: 0,
            bottom: 0,
            paddingTop: '120px',
            display: displayMenu,
            flexDirection: 'column',
            alignItems: 'flex-end',
            paddingRight: '50px',
        },
        line: {
            position: 'fixed',
            width: barWidth,
            right: 0,
            top: 0,
            bottom: 0,
            background: 'linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(0,0,0,1) 85%)',
            transition: 'width .5s',
        },
    }

    return (
        <>
            <div style={styles.line}></div>
            <div style={styles.container}>
                <Option title='Strona główna' link='/' />
                <Option title='Oferta' link='/' />
                <Option title='Kadra' link='/team' />
                <Option title='Aktualności' link='/news' />
                <Option title='Cennik' link='/' />
                <Option title='Dla rodziców' link='/' />
                <Option title='Kontakt' link='/' />
                <div style={{ margin: '15vh 0 5vh 0' }}>
                    <FontAwesomeIcon icon={faFacebookSquare} color={socialColor1} cursor='pointer' size='3x' style={{ marginRight: '10px' }} onMouseOver={() => { setSocialColor1('#4267B2') }} onMouseLeave={() => { setSocialColor1('white') }} />
                    <FontAwesomeIcon icon={faInstagram} size='3x' cursor='pointer' style={{ background: socialColor2, borderRadius: '25%', padding: '0 3 0 3' }} onMouseOver={() => { setSocialColor2('-webkit-linear-gradient(203deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%)') }} onMouseLeave={() => { setSocialColor2('white') }} />
                </div>
            </div>
            <Hamburger clickHandler={startMenu} menuStatus={menuState} />
        </>
    );
};

export default Menu;
