import React, { useState, useEffect } from 'react';

const Hamburger = ({ clickHandler, menuStatus }) => {

    const [transforme1, setTransforme1] = useState('rotate(0deg)');
    const [transforme2, setTransforme2] = useState('rotate(0deg)');
    const [transforme3, setTransforme3] = useState('rotate(0deg)');
    const [colorHam, setColorHam] = useState('black');

    useEffect(() => {
        if (menuStatus) {
            setTransforme1('rotate(50deg) translate(10px, 7px)');
            setTransforme2('rotate(50deg) translate(5px, -4px)');
            setTransforme3('rotate(-50deg) translate(15px, -10px) scale(1.2, 1)');
            setColorHam('white');
        } else {
            setTransforme1('rotate(0deg)');
            setTransforme2('rotate(0deg)');
            setTransforme3('rotate(0deg)');
            setColorHam('black');
        }
    }, [menuStatus]);

    const styles = {
        containerBar: {
            width: 60,
            height: 50,
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'space-around',
            alignItems: 'center',
            position: 'fixed',
            right: 30,
            top: 30,
            cursor: 'pointer',
        },
        bar1: {
            width: 50,
            height: 10,
            backgroundColor: colorHam,
            borderRadius: 15,
            transform: transforme1,
            transition: 'transform .2s, background-color .3s',
        },
        bar2: {
            width: 50,
            height: 10,
            backgroundColor: colorHam,
            borderRadius: 15,
            transform: transforme2,
            transition: 'transform .2s, background-color .3s',
        },
        bar3: {
            width: 50,
            height: 10,
            backgroundColor: colorHam,
            borderRadius: 15,
            transform: transforme3,
            transition: 'transform .2s, background-color .3s',
        }
    }

    return (
        <div style={styles.containerBar} onClick={clickHandler}>
            <div style={styles.bar1}></div>
            <div style={styles.bar2}></div>
            <div style={styles.bar3}></div>
        </div>
    );
};

export default Hamburger;