import React, { useState, useEffect } from 'react';
import Logo from '../../img/logo.png';
import boss from '../../img/team/Maria.jpg';

const Main = () => {

    const [showAbout, setShowAbout] = useState(false);
    const [showAboutOpacity, setShowAboutOpacity] = useState(0);
    const [showAboutTextOpacity, setShowAboutTextOpacity] = useState(0);

    useEffect(() => {
        setTimeout(() => { setShowAboutOpacity(1) }, 1000);
    }, []);

    const showAboutUs = () => {
        setShowAboutOpacity(0);
        setTimeout(() => { setShowAbout(true); }, 1000);
        setTimeout(() => { setShowAboutTextOpacity(1) }, 2000);
    };

    const styles = {
        container: {
            minHeight: "96vh"
        },
        logo: {
            background: 'linear-gradient(180deg, rgba(255,255,255,1) 30%, rgba(255,255,255,0) 100%)',
            display: 'flex'
        },
        nameContainer: {
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            paddingLeft: '20px',
        },
        image: {
            width: '30%',
        },
        title: {
            textAlign: 'center',
            margin: 0,
            padding: 0,
            color: '#0B96B5',
            fontFamily: 'Architects Daughter',
            fontSize: '70px',
        },
        text: {
            margin: 0,
            padding: 0,
            color: 'black',
            fontFamily: 'Architects Daughter',
            fontSize: '40px',
        },
        content: {
            opacity: showAboutOpacity,
            marginTop: '20vh',
            fontFamily: 'Architects Daughter',
            fontSize: '30px',
            textAlign: 'center',
            transition: 'opacity 1s',
        },
        boton: {
            fontSize: '25px',
            fontFamily: 'Architects Daughter',
            background: '#0B96B5',
            border: '2px solid black',
            borderRadius: '10%',
            width: '150px',
            height: '65px',
            cursor: 'pointer',
            marginBottom: '20px',
        },
        aboutUs: {
            width: '50vw',
            fontFamily: 'Architects Daughter',
            fontSize: '19px',
            marginLeft: '20px',
            borderRadius: '10%',
        },
        aboutUsImage: {
            height: '55vh',
            alignSelf: 'center',
        },
    }

    return (
        <div style={styles.container}>
            <div style={styles.logo}>
                <div style={styles.nameContainer}>
                    <h1 style={styles.title}>Akademia</h1>
                    <p style={styles.text}>Centrum edukacji i animacji</p>
                </div>
                <img style={styles.image} src={Logo} alt="src"/>
            </div>
            { showAbout ?
                <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', marginTop: '20px', background: 'radial-gradient(circle, rgba(255,255,255,1) 0%, rgba(255,255,255,0) 100%)', opacity: showAboutTextOpacity, transition: 'opacity .5s', borderTop: '3px solid #0B96B5', borderBottom: '3px solid #0B96B5', }}>
                    <img style={styles.aboutUsImage} src={boss} alt="src"/>
                    <div style={styles.aboutUs}>
                        <h2>Witamy w Akademi</h2>
                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Cras magna nulla, vestibulum sit amet ultrices sed, lacinia sed mi. Vestibulum lacinia orci in suscipit luctus. Suspendisse consequat elit ut accumsan suscipit. Vestibulum hendrerit, arcu sit amet ornare aliquam, nibh augue porta tortor, eu dignissim enim sapien et massa. Suspendisse vestibulum ultricies eros eget convallis. Maecenas aliquet leo vitae enim sagittis accumsan. Donec eleifend eu lorem a mattis. Nunc molestie dui nisl, in placerat est consequat vel. Fusce ac magna tincidunt lorem bibendum pharetra. In iaculis velit eget vestibulum pharetra. Mauris aliquet at enim ac blandit. Curabitur a mi sit amet nibh auctor dignissim at id massa. Duis ut ex quis odio ornare consectetur.</p>
                        <p>Nulla facilisi. Sed id viverra neque. Proin augue tortor, faucibus at dictum et, gravida quis est. Fusce lacinia eu ligula et iaculis. Morbi quis erat id felis venenatis venenatis. Nullam dignissim rhoncus nisi ac feugiat. Etiam dolor enim, tincidunt a pulvinar ut, bibendum eget nisl. Suspendisse posuere dui nisi, ac rutrum lorem facilisis in. Praesent porttitor scelerisque mi, sed dignissim diam dictum eget. Aenean iaculis, tellus et placerat mattis, urna enim hendrerit nisl, nec sollicitudin ex dolor vitae tortor. Vestibulum ac leo eget mi semper ultrices. Cras a quam tincidunt, posuere dolor suscipit, accumsan velit. Aenean metus erat, condimentum sed mauris vitae, pretium ullamcorper ante. Mauris at nulla ut urna interdum viverra vitae quis magna. Duis ut ornare arcu. Duis vestibulum dictum lobortis.</p>
                        <p style={{ textAlign: 'right', marginRight: '30px' }}>Maria Kowalska</p>
                    </div>
                </div>
                :
                <div style={styles.content}>
                    <h2>Akademia</h2>
                    <p>Nowoczesne centrum terapi dziecięcej i rozwoju...</p>
                    <button onClick={showAboutUs} style={styles.boton}>Poznaj nas</button>
                </div>}
        </div>
    );
};

export default Main;