import React from 'react';
import TeamComponent from '../../Components/teamComponent/teamComponent';
import Img1 from '../../img/team/Anna.jpg';
import Img2 from '../../img/team/Krystian.jpg';
import Img3 from '../../img/team/Maria.jpg';

const Team = () => {

    const information = {
        team: [
            {
                name: "Maria",
                about: "front-end developer",
                img: Img1
            },
            {
                name: "Anna",
                about: "CEO team",
                img: Img3
            },
            {
                name: "Krystian",
                about: "back-end developer",
                img: Img2
            }
        ]
    }

    const styles={
        container: {
            minHeight: "96vh",
            boxSizing: "border-box",
            fontFamily: 'Architects Daughter'
        },
        span: {
            display: 'block',
            width: '100%',
            padding: '20px',
            lineHeight: '100px',
            fontSize: '50px',
            textAlign: 'center',

        },
        team: {
            display: 'flex',
            justifyContent: 'space-around',
            width: '100%'
        }
    }

    const team = information.team.map(item =>
        <TeamComponent 
            name={item.name} 
            about={item.about} 
            img={item.img}/>
    )

    return(
        <div style={styles.container}>
            <span style={styles.span}>Nasza kadra</span>
            <div style={styles.team}>
                {team}
            </div>
        </div>
    )
}

export default Team;