import React from 'react';

const BigNewsComponent = ({ setArticleVisible, news }) => {

    const styles = {
        container: {
            display: "flex",
            flexDirection: "column",
            justifyContent: "space-between",
            position: "fixed",
            top: "8%",
            left: "8%",
            boxSizing: "border-box",
            width: "84vw",
            height: "84vh",
            padding: "20px",
            fontFamily: 'Architects Daughter',
            color: "white",
            border: "2px solid black",
            borderRadius: "10px",
            background: "rgba(0%,0%,0%,80%)"
        },
        title: {
            height: "10%",
            fontSize: "30px",
            textAlign: "center"
        },
        containerImg:{
            width: "100%",
            height: "50%",
            textAlign: "center"
        },
        img: {
            height: "80%",
            border: "2px solid white"
        },
        article: {
            fontSize: "20px"
        },
        containerButton: {
            textAlign: "center"
        },
        button: {
            padding: "20px 50px",
            fontSize: '25px',
            fontFamily: 'Architects Daughter',
            background: '#0B96B5',
            border: '2px solid black',
            borderRadius: '10%',
            cursor: 'pointer',
            marginBottom: '20px',
        }
    }

    return(
        <div style={styles.container}>
            <div style={styles.title}> {news.title} </div>
            <div style={styles.containerImg}>
                <img src={news.img} style={styles.img} alt="src"></img>
            </div>
            <div style={styles.article}>{news.short}</div>
            <div style={styles.containerButton} >
                <button onClick={ ()=>setArticleVisible(false) } style={styles.button}>Click Me</button>
            </div>
        </div>
    )
}

export default BigNewsComponent;