import React, { useState } from 'react';

const NewsComponent = ({ key, title, about, short, img, setArticleVisible, setIndexArticle, id }) => {

    const [bgcButton, setBgcButton] = useState('#0B96B5')

    const onButton = () => {
        setBgcButton("#1B8594")
    }

    const offButton = () => {
        setBgcButton("#0B96B5")
    }

    const handleClick = () => {
        setArticleVisible(true);
        setIndexArticle(id);
    }

    const styles = {
        container: {
            display: "flex",
            flexDirection: "column",
            justifyContent: "space-between",
            width: '28%',
            height: "550px",
            margin: "10px",
            padding: '10px 10px 0 10px',
            fontFamily: 'Architects Daughter',
            border: '5px solid #0B96B5',
            borderRadius: "15px",
            backgroundColor: 'rgba(255%,255%,255%,30%)',
            fontSize: '30px'
        },
        title: {
            height: "90px",
            fontSize: '30px',
            textAlign: "center"
        },
        about: {
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'space-beetwen',
            width: '70%',
            fontSize: '12px'
        },
        img: {
            height: "250px",
            margin: "5px 0",
            border: "2px solid black",
            backgroundColor: "white"
        },
        tile: {
            display: 'flex',
            justifyContent: 'center',
            width: '25%',
            fontSize: '14px',
            fontFamily: "roboto",
            letterSpacing: '1px',
            color: 'black'
        },
        short: {
            fontSize: '18px',
            lineHeight: "25px",
            height: "100px",
            textAlign: 'justify',
            overflow: "hidden"
        },
        buttonContainer: {
            display: 'flex',
            justifyContent: 'center',
            margin: "10px 0"
        },
        button: {
            padding: "10px 25px",
            fontSize: '20px',
            fontFamily: 'Architects Daughter',
            background: bgcButton,
            border: '2px solid black',
            borderRadius: '10%',
            cursor: 'pointer',
            transition: 'background-color 1s',
        }
    }

    return (
        <div style={styles.container}>
            <header style={styles.title}>{title}</header>
            <div style={styles.about}>
                <div style={styles.tile}>{about.date}</div>
                <div style={styles.tile}>{about.category}</div>
                <div style={styles.tile}>{about.author}</div>
                <div style={styles.tile}>{about.status}</div>
            </div>
            <img src={img} style={styles.img} alt="src"></img>
            <div style={styles.short}>{short}</div>
            <div style={styles.buttonContainer}>
                <button onClick={() => handleClick()} onMouseOver={() => onButton()} onMouseLeave={() => offButton()} style={styles.button}>Czytaj więcej</button>
            </div>
        </div>
    )
}

export default NewsComponent;