import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';

const Option = ({ title, link }) => {

    const [border, setBorder] = useState('translate(210px, 0px)');

    const styles = {
        textContainer: {
            marginBottom: '4vh',
        },
        text: {
            fontSize: '25px',
            fontFamily: 'Arial',
            color: 'white',
            margin: 0,
            cursor: 'pointer',
            // eslint-disable-next-line
            fontFamily: 'Roboto Condensed',
            fontWeight: 'bold',
        },
        border: {
            height: '3px',
            backgroundColor: '#0B96B5',
            transform: border,
            transition: 'transform .5s',
        }
    }

    return (
        <NavLink to={link} style={{ textDecoration: 'none' }}>
            <div style={styles.textContainer} onMouseOver={() => { setBorder('translate(0px, 0px)') }} onMouseLeave={() => { setBorder('translate(210px, 0px)') }} >
                <p style={styles.text}>{title}</p>
                <div style={styles.border}></div>
            </div>
        </NavLink>
    );
};

export default Option;