import React from 'react';

const TeamComponent = ({name, about, img}) => {

    const styles = {
        conrainer: {
            boxSizing: "border-box",
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            fontFamily: 'Architects Daughter',
            width: '25%',
            padding: '5px',
            border: '5px solid #0B96B5',
            borderRadius: "10px",
            backgroundColor: 'rgba(80%,80%,80%,100%)'
        },
        img: {
            width: '100%',
            border: "1px solid rgba(70%,70%,70%,100%)"
        },
        name: {
            fontSize: "34px",
            letterSpacing: "2px"
        }
    }

    return(
        <div style={styles.conrainer}>
            <img src={img} alt="zaloga" style={styles.img}/>
            <div style={styles.name}>{name}</div>
            
            <article>{about}</article>
        </div>
    )
}

export default TeamComponent;